div[data-reactroot] {
    & > .list {
        column-count: 3;

        &.selected {
            column-count: 1;

            & > .list-item > *:first-child {
                display: inline-block;
                cursor: auto;

                & > img {
                    width: auto;
                    max-width: 30em;
                    max-height: 30em;
                }
            }

            & > .list-item:not(.selected) {
                display: none;
            }
        }

        & > .list-item {
            width: 100%;
            min-height: 0;
            margin: 0 1em 1em 0;
            display: block;

            & > div {
                display: block;

                & > img {
                    width: 100%;
                }
            }
        }
    }
}



